.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

body {
	margin: 0;
	font-family: "Cabin", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	opacity: 1;
	box-sizing: border-box;
}

h1 {
	font-size: 2.5rem;

	font-weight: 500;
}

h2 {
	font-size: 1.5rem;
	font-weight: 600;
}

h3 {
	font-size: 1.17rem;
}

h4 {
	font-size: 1rem;
}

p {
	font-size: 1.2rem;
	font-weight: 400;

	padding-bottom: 1rem;
}

span {
	font-size: 1.4rem;
	font-weight: 400;
}
@media screen and (max-width: 820px) {
	h2 {
		font-size: 1rem;
	}
	p {
		font-size: 0.8rem;
	}
}
